button {}

.wrapper {
    height: 100%;
    background-image: url("../../public/images/bg-texture-white.png");
    background-color: #E0E0D8;
    background-color: #b9b990;
    background-color: #3c657a69;
    background-color: #ffffffd1;
    background-color: #00000054;
    display: flex;
    flex-direction: column;
}

.filters {}

.wrapper>ul {
    max-width: 1200px;
    margin: 0 auto;
    gap: 10px !important;
    flex-grow: 1;
}

.placeholder {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 25px;
    padding-top: 50px;
}

.wrapper>ul>li {
    position: relative;
    box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
    cursor: pointer;
}

.wrapper>ul>li .favorite {
    position: absolute;
    bottom: 5%;
    right: 5%;
}

.favorite svg {
    transition: 0.5s;
    fill: transparent;
}

button {
    background: transparent;
    border: none;
    cursor: pointer;
}

.favorite svg {
    width: 35px;
}

.added svg {
    fill: #CC4444;
    fill: #fff;
    fill: #ffe4c4;
}

.favorite.added svg,
.wrapper>ul>li .favorite:hover svg {
    fill: #CC4444;
    fill: #fff;
    fill: #ffe4c4;
}